
import { computed, defineComponent } from 'vue';
export default defineComponent({
  props: {
    addressData: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  setup(prop) {
    const sellAddress = computed(() => {
      const sellAdd = prop.addressData.sellAddress.split('/');
      return `${sellAdd[0]}${sellAdd[1]} ${sellAdd[2]}`;
    });
    return {
      sellAddress
    };
  }
});
