
import { computed, defineComponent } from 'vue';
export default defineComponent({
  props: {
    orderItem: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  setup(props) {
    const freightType = computed(() => {
      let freightType = '';
      switch (props.orderItem.deliveryType) {
        case 0:
          freightType = '快递';
          break;
        case 1:
          freightType = '物流';
          break;
        case 2:
          freightType = '自提';
          break;
        case 3:
          freightType = '送货上门';
          break;
      }
      return freightType;
    });
    const refundType = computed(() => {
      let refundType = '';
      switch (props.orderItem.buyerDeliveryType) {
        case 0:
          refundType = '快递';
          break;
        case 1:
          refundType = '物流';
          break;
        case 2:
          refundType = '送货上门';
          break;
      }
      return refundType;
    });
    return {
      freightType,
      refundType
    };
  }
});
